@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


body {
  font-family: 'Lobster', cursive;
  background: linear-gradient(45deg, #bad7ee, #02aefd);
}

#quote-box {
  background-color: rgb(248, 245, 245);
  width: 40em;
  margin: 10em auto;
  padding: 3em;
  border-radius: 5px;
}

#text {
  font-size: 2em;
  margin-bottom: 1.5em;
}

#author {
  display: flex;
  justify-content: flex-end;
  font-size: 1.2em;
}

#buttons {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 2em;
}

.social-media {
  display: flex;
  align-items: flex-start;
}

#new-quote {
  background-color: #02aefd;
  border: none;
  padding: .7em 1em;
  font-size: .9em;
  border-radius: 2px;
  font-weight: bold;
  outline: none;
  cursor: pointer;
}

#new-quote:hover {
  opacity: 0.9;
}